import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  HostListener,
  OnInit,
  ViewChild
} from '@angular/core';
import { IManagedObject, IResultList, QueriesUtil } from '@c8y/client';
import {
  Column,
  FilteringFormRendererContext,
  ForOfFilterPipe,
  gettext,
  TypeaheadComponent
} from '@c8y/ngx-components';
import { RepositoryService, RepositoryType } from '@c8y/ngx-components/repository/shared';
import { cloneDeep, uniqBy } from 'lodash-es';
import { BehaviorSubject, NEVER, Observable, pipe } from 'rxjs';
import { debounceTime, map, switchMap, tap } from 'rxjs/operators';

@Component({
  templateUrl: './software-type.filtering-form-renderer.component.html',
  changeDetection: ChangeDetectionStrategy.Default
})
export class SoftwareTypeFilteringFormRendererComponent implements OnInit, AfterViewInit {
  selectedType: IManagedObject;
  softwareWithType$: Observable<IResultList<IManagedObject>> = NEVER;
  search$: BehaviorSubject<string> = new BehaviorSubject(null);
  filterPipe: ForOfFilterPipe<IManagedObject> = pipe(tap());
  typeaheadPlaceholder = gettext('Start typing to search, for example, {{ example }}');
  @ViewChild(TypeaheadComponent, { static: false }) typeahead: TypeaheadComponent;
  private queriesUtil: QueriesUtil = new QueriesUtil();
  private softwareTypes: Set<string> = new Set();

  constructor(
    public context: FilteringFormRendererContext,
    public changeDetectorRef: ChangeDetectorRef,
    private repositoryService: RepositoryService,
    private elementRef: ElementRef
  ) {
    this.softwareWithType$ = this.search$.pipe(
      debounceTime(300),
      tap(() => this.softwareTypes.clear()),
      switchMap((searchString: string) => {
        let query = this.queriesUtil.prependOrderbys({}, [{ softwareType: 1 }]);
        const filter = !!searchString
          ? {
              softwareType: {
                __eq: `*${searchString}*`
              }
            }
          : {
              __has: 'softwareType'
            };
        query = this.queriesUtil.addAndFilter(query, filter);

        return this.repositoryService.listRepositoryEntries(RepositoryType.SOFTWARE, {
          skipDefaultOrder: true,
          query,
          params: {
            pageSize: 200
          }
        });
      })
    );

    this.filterPipe = pipe(
      map(
        this.removeDuplicatesBySoftwareType.bind(
          this
        ) as SoftwareTypeFilteringFormRendererComponent['removeDuplicatesBySoftwareType']
      ),
      tap(() => this.changeDetectorRef.detectChanges())
    );
  }

  @HostListener('keyup.enter', ['$event']) onEnterKeyUp(event: KeyboardEvent) {
    event.stopPropagation();
    this.applyFilter();
  }
  @HostListener('keydown.escape', ['$event']) onEscapeKeyDown(event: KeyboardEvent) {
    event.stopPropagation();
    this.context.resetFilter();
  }

  ngOnInit(): void {
    const column: Column = this.context.property;
    this.selectedType = cloneDeep(column.externalFilterQuery || {});
  }

  ngAfterViewInit(): void {
    this.typeahead?.searchControl?.nativeElement?.focus();
    try {
      this.elementRef.nativeElement.parentElement.parentElement.style.overflow = 'visible';
    } catch (ex) {
      // intentionally empty
    }
  }

  applyFilter() {
    this.context.applyFilter({
      externalFilterQuery: this.selectedType
    });
  }

  private removeDuplicatesBySoftwareType(list: IManagedObject[]): IManagedObject[] {
    const uniqueBySoftwareType: IManagedObject[] = uniqBy(list, 'softwareType').filter(
      (sw: IManagedObject) => !this.softwareTypes.has(sw.softwareType)
    );
    uniqueBySoftwareType.forEach((sw: IManagedObject) => this.softwareTypes.add(sw.softwareType));
    return uniqueBySoftwareType;
  }
}
